
import {defineComponent} from 'vue';
import {Cast} from "@/helpers/types/MovieType";

export default defineComponent({
    name: "PeopleItem",
    props: {
        people: {
            type: Object as () => Cast,
            required: true
        },
    },
    data() {
        return {
            peopleIsHoverID: 0,
        };
    },
    methods: {
        handleSearch() {
            this.$router.push({name: 'PEOPLE', params: {id: this.people.id}});
        }
    },
})
